"use scrict";
import MyForm from "@/components/MyForm/index.vue";
import MyFormItem from "@/components/MyFormItem/index.vue";
import MySelect from "@/components/MySelect/index.vue";
import { NOTICE, NOTICE_SELECT_RECEIVE } from "@/config/router.js";
import { time } from "@/utils/util.js";

export default {
	data() {
		return {
			noticeTypeOptions: [],
			receive: [],
			detail:{
				title: "",
				receiverIds: "",
				categoryId: "",
				content: "",
				inscription: "",
				isSendToSelf: 0,
				notifyTime: ""
			},
			rules: {
				title: "通知标题不能为空",
				receiverIds: "接收对象不能为空",
				categoryId: "通知类型不能为空",
				content: "通知内容不能为空"
			},
			inSubmit: false,
			sendTime: {
				date: "",
				time: ""
			}
		}
	},
	computed: {
		receiveName: {
			get() {
				let res = [];
				this.receive.forEach(item => res.push(item.name));
				return res.join(',');
			},
			set(value) {
				this.receive = value;
				let res = [];
				value.forEach(item => res.push(item.id))
				this.detail.receiverIds = res;
			}
		},
		bindSendDate: {
			get() {
				return this.sendTime.date;
			},
			set(value) {
				this.sendTime.date = value;
				this.sendTime.time = this.sendTime.time || "00:00";
				this.$set(this.detail, "notifyTime", `${value} ${this.sendTime.time}:00`);
			}
		},
		bindSendTime: {
			get() {
				return this.sendTime.time;
			},
			set(value) {
				this.sendTime.time = value;
				this.sendTime.date = this.sendTime.date || time(Date.now());
				this.$set(this.detail, "notifyTime", `${this.sendTime.date} ${value}:00`);
			}
		}
	},
	onLoad(options) {
		this.$eventBus.$on("setNoticeReceive", value => {
			if (!Array.isArray(value) || value.length === 0) return;
			this.receiveName = value;
		});
		this.$post(this.$api.GET_CATEGORY_LIST, {type: 2}).then(res => {
			this.noticeTypeOptions = res.data;
		});
	},
	methods: {
		clearReceive: function () {
			this.detail.receive = "";
			this.receive = [];
		},
		linkToSelectReceive: function () {
			this.$toPage(NOTICE_SELECT_RECEIVE, {
				source: "notice"
			});
		},
		selectSendSelf: function (e) {
			this.detail.isSendToSelf = Number(!!e.target.value[0]);
		},
		submit: function () {
			this.inSubmit = true;
			this.$post(this.$api.SEND_NOTICE, this.detail).then(res => {
				this.$toast({
					title: "通知发送成功",
					icon: "success"
				});
				this.$eventBus.$emit("setNoticeIndex", 0);
				this.$eventBus.$emit("updateNoticeList", 1);
				this.$eventBus.$emit("updateNoticeList", 2);
				this.$toPage(NOTICE);
			});
			this.inSubmit = false;
		}
	},
	components: {
		MyForm,
		MyFormItem,
		MySelect
	}
}
